* {
  box-sizing: border-box;
  margin: 0;
}

:root {
  --drumpad-width: 100px;
  --drumpad-height: 100px;
}

body {
  background-color: #ffedc3;
  color: white;
}

.drum-container {
  margin: 13rem auto;
  width: 900px;
  height: 500px;
  text-align: center;
  background-color: aquamarine;
  border-radius: 10px;
  box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.15),
    0px 0px 4px 0px rgba(0, 0, 0, 0.4);
  padding: 1rem;
}

.option-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  min-height: 90%;
}

.panel-options {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: calc(var(--drumpad-height) * 3 + 32px);
  font-size: 1.1rem;
  font-weight: 700;
}

.on-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  transition: all 0.2s ease-in-out;
}

.on-button.active {
  background-color: rgb(255, 24, 24);
}

.drum-display {
  width: 100%;
  height: 40px;
  color: white;
  background-color: black;
  border-radius: 10px;
  padding: 0.5rem;
}

.bank-switch-container {
  width: 80px;
  height: 30px;
  background-color: white;
  border-radius: 20px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  position: relative;
}

.bankswitch {
  width: 30px;
  height: 30px;
  background-color: black;
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
  position: absolute;
}

.bankswitch.active {
  transform: translateX(50px);
}
.drumbank-style {
  display: flex;
  flex-wrap: wrap;
  width: calc(var(--drumpad-width) * 3 + 32px);
  height: calc(var(--drumpad-height) * 3 + 32px);
}

.drumpad {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: larger;
  margin-left: 10px;
  margin-bottom: 10px;
  width: var(--drumpad-width);
  height: var(--drumpad-height);
  background-color: rgb(249, 120, 156);
  border-radius: 5px;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  transition: all 0.2 ease-in-out;
}

.drumpad.active {
  background-color: rgb(253, 83, 131);
  transform: scale(95%);
}
